*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family:cursive;
  font-weight: 900;
  user-select: none;
}
body {
  width: 100vw;
  height: 100vh;
}
#root{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  position: relative;
  background: radial-gradient(ellipse farthest-side at 76% 77%, rgba(245, 228, 212, 0.25) 4%, rgba(255, 255, 255, 0) calc(4% + 1px)), radial-gradient(circle at 76% 40%, #fef6ec 4%, rgba(255, 255, 255, 0) 4.18%), linear-gradient(135deg, #ff0000 0%, #000036 100%), radial-gradient(ellipse at 28% 0%, #ffcfac 0%, rgba(98, 149, 144, 0.5) 100%), linear-gradient(180deg, #cd6e8a 0%, #f5eab0 69%, #d6c8a2 70%, #a2758d 100%); background-blend-mode: normal, normal, screen, overlay, normal;
}
.main h1{
  top: 80px;
  left: 50%;
  transform: translate(-50%,0);
  padding: 0px;
  font-size: 1.5em;
  font-family:cursive;
  position: absolute;
  color: #c95498;
}
.main .playarea .winner{
  padding-top: 50px;
}
.main .playarea .winner h3{
  padding: 10px 0;
  color: orangered;
  text-shadow: 1px 0px black;
}
.main .playarea .element{
  border-radius: 50%;
  margin: 5px;
  transition: 0.5s;
  padding: 5px;
  box-shadow: 0 2px rgb(36, 36, 36);
}
.main .playarea .element:hover{
  transform: scale(1.5);
}
.main .playarea .element button:active{
  transform: translate(0,-50%);
}
.main .playarea .element button{
  width: 70px;
  height: 70px;
  transition: 0.5s;
  border:none;
  border-radius: 50%;
  outline: none;
  color: white;
  background: rgb(107, 107, 107);
}
.main .playarea .element:nth-child(1){
  background: #c95498;
}
.main .playarea .element:nth-child(2){
  background: #b554c9;
}
.main .playarea .element:nth-child(3){
  background: #5479c9;
}
.red{
  color: red;
}
.green{
  color: green;
  transform: scale(1.5) translate(10%,30%);
}
.white{
  color: orange;
}
